@import url('https://fonts.googleapis.com/css2?family=Handlee&family=Outfit:wght@100..900&display=swap');

h1, h2, h3, p, a {
  font-family: "Handlee", Calibri, sans-serif;
}

label, input, button {
  font-family: "Outfit", Arial, sans-serif;
}

$lighter-primary-color: #eca554;
$primary-color: #9f6a2f;
$darker-primary-color: #4f3517;
$secondary-color: #FFC84D;
$darkest-secondary-color: #5e4a20;
$secondary-background-color: #5dc6e7;
$darker-secondary-background-color: #234a56;

a img {
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: sans-serif;
  background-image: url('../images/backround.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

button[type="submit"] {
  background-color: #9f6a2f;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 12px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #4f3517;
  }
}

form {
  text-align: center;

  button {
    border-radius: 10px;
    border: 3px solid $darker-primary-color;
    padding: 5px;
    background-color: $secondary-color;
    color: $darkest-secondary-color;
    font-size: 1em;

    &:hover {
      background-color: $darkest-secondary-color;
      color: white;
      border: 3px solid $lighter-primary-color;
    }
  }

  div {
    padding: 10px;

    input {
      border-radius: 10px;
      height: 20px;
      box-shadow: none;
      border: 3px solid $darker-primary-color;
      outline: none;
      padding: 5px;
      width: 100%;
      box-sizing: border-box;
    }
  }
}

h2 {
  font-size: 2em;
  margin: 10px 0;
  text-align: center;
  color: white;
}

label {
  color: white;
}

html {
  background-image: url('../images/Sunny Garden Haven.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

svg {
  width: 80px;
  height: 80px;
}

/* --- Layout --- */

.top-bar {
  flex: 0 0 80px;
  position: sticky;
  top: 0;
  z-index: 10;
  height: 80px;
  background-color: rgb(49, 152, 49);
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-top: 2rem;
  padding-bottom: 1rem;
}

.left-image {
  height: 50px;
  width: auto;
  position: absolute;
  left: 1rem;
  bottom: 10px;
}

.top-bar > svg:not(.left-image) {
  height: 100px;  // bigger than the left SVG
  width: auto;
  margin: 0 auto;
}

.content {
  flex: 1 1 auto; /* grow and shrink, fill remaining space */
  overflow-y: auto; /* scroll if overflow */
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  box-sizing: border-box;
}

.plant {
  background-color: rgba(0, 0, 0, 0.75);
  text-decoration: none;
  outline: none;
  border-radius: 15px;
  padding: 10px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 170px;
  transition: background-color 0.3s;

  p, h3 {
    margin: 5px 0;
  }

  &:visited {
    text-decoration: none;
    outline: none;
  }

  &:hover {
    background-color: rgba(0, 0, 0, 0.9);
  }
}


.bottom-bar {
  flex: 0 0 130px;
  position: fixed;
  width: 100%;
  bottom: 0 /*or 23%*/;
  left: 0;
  z-index: 10;
  background-color: rgb(49, 152, 49);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.bottom-icon {
  height: 60px;
  width: auto;
}

.login-container {
  background-image: url('../images/Wooden Signboard with Natural Grain.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  
  width: 320px;
  padding: 30px 25px;
  border-radius: 20px;
  
  /* Make sure it can grow if needed */
  max-width: 90vw;
  box-sizing: border-box;
  text-align: center;
  
  /* text colors */
  color: white;

  h2 {
    margin-top: 2rem;
    font-size: 2em;
  }
  
  form {
    display: flex;
    flex-direction: column;
    padding: 15px;
    gap: 15px;

    div {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      label {
        margin-bottom: -5px;
        color: white;
        font-weight: 600;
      }

      input {
        border-radius: 10px;
        height: 35px;
        border: 3px solid $darker-primary-color;
        outline: none;
        padding: 0 10px;
        width: 100%;
        box-sizing: border-box;
        font-size: 1em;
      }
    }

    button {
      border-radius: 10px;
      border: 3px solid $darker-primary-color;
      padding: 5px;
      margin: 25px 10px;
      background-color: $secondary-color;
      color: $darkest-secondary-color;
      font-size: 1.1em;
      font-weight: 700;
      cursor: pointer;
      transition: background-color 0.3s ease, color 0.3s ease;

      &:hover {
        background-color: $darkest-secondary-color;
        color: white;
        border-color: $lighter-primary-color;
      }
    }
  }
}

// Add Plant Form
.add-habit-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
}

.add-habit-form {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  padding: 30px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.plant-picker {
  display: flex;             /* horizontal flex container */
  justify-content: center;   /* center the items horizontally */
  align-items: center;       /* vertically center the circles */
  gap: 15px;                 /* space between the circles */
  margin-bottom: 20px;
  flex-direction: row;       /* explicitly set horizontal */
}

.plant-picker .plant-circle {
  border-radius: 50%;
  background-color: #292929;
  cursor: pointer;
  transition: transform 0.3s ease, opacity 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border: 3px solid transparent;
  margin: 0;
}

.plant-picker .center {
  width: 120px;
  height: 120px;
  opacity: 1;
  border-color: #9f6a2f; /* primary color */
  transform: translateY(0);
  z-index: 2;
}

.plant-picker .side {
  width: 90px;
  height: 90px;
  opacity: 0.5;
  transform: translateY(15px);  /* slightly lower for perspective */
  z-index: 1;
}

.habit-title {
  margin-top: 15px;
  font-size: 1.2em;
  font-weight: bold;
  color: white;
}

.goal-name-box {
  background-color: #f3f3f3;
  border-radius: 15px;
  padding: 10px 15px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  border: 2px solid #ccc;
}

.goal-name-box input {
  flex: 1;
  border: none;
  outline: none;
  font-size: 1em;
  background-color: transparent;
  color: #555;
}

.goal-name-box .pencil-icon {
  color: #888;
  margin-left: 10px;
  pointer-events: none;
}

.frequency-section {
  text-align: center;
  margin-bottom: 20px;

  h3 {
    font-size: 1.1em;
    color: white;
    margin-bottom: 10px;
  }

  input {
    width: 70%;
    padding: 8px;
    border-radius: 10px;
    border: 2px solid #ccc;
    outline: none;
    font-size: 1em;
  }
}

.welcomeMessage{
  background-color: white;
  padding: 20px;
  height: 100%;
  min-height: 690px;
  min-width: 500px;

  &_header{
    background-color: darkgray;
    padding: 20px;
    border-radius: 20px;

    h1{
      margin-top: 40px;
      margin-bottom: 0px;
      text-align: center;
      font-size: 3em;
      color: white;
      text-shadow: 0px 10px 5px #000000;

    }

    h3{
      text-align: center;
      margin-top: 0px;
    }
  }

  button{
    float: right;
    border-radius: 15px;
    padding: 5px;
    border: 3px solid darkgray;

  }
}


/* Button without background */
.add-plant-button {
  background-color: gray;
  border: 3px solid darkgray;
  border-radius: 50%;
  padding: 10px;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px -5px 15px rgba(0, 0, 0, 0.3);
  position: relative;
  top: -50px;
  z-index: 2;
  transition: transform 0.2s ease;

  &:hover {
    background-color: darkgray;
    transform: scale(1.05);
  }

  svg {
    width: 50px;
    height: 50px;
  }
}

