@media screen and (min-width: 35em) {
  svg {
    width: 100px;
    height: 100px;
  }
  /*Classes*/
  .bottom-bar {
    bottom: 0;
    width: 100%;
    max-width: 500px;
    left: auto;
  }
  .content {
    max-width: 770px;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    padding: 20px;
  }
  .left-image {
    height: 80px;
  }
  .plant {
    height: 185px;
  }
  .top-bar {
    height: 90px;
    padding: 0 2rem;
  }
}
@media screen and (min-width: 80em) {
  body {
    background-size: cover;
  }
  .bottom-bar {
    max-width: 775px;
  }
  .top-bar {
    height: 130px;
  }
  svg {
    width: 120px;
    height: 120px;
  }
  .left-image {
    height: 100px;
  }
  .content {
    max-width: 770px;
    grid-template-columns: 225px 225px 225px;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 30px;
  }
  /* Reapply plant grid placement if needed */
  #plant1 {
    grid-column: 2;
    grid-row: 1;
  }
  #plant2 {
    grid-column: 3;
    grid-row: 1;
  }
  #plant3 {
    grid-column: 4;
    grid-row: 1;
  }
  #plant4 {
    grid-column: 2;
    grid-row: 2;
  }
  #plant5 {
    grid-column: 3;
    grid-row: 2;
  }
  #plant6 {
    grid-column: 4;
    grid-row: 2;
  }
  #plant7 {
    grid-column: 2;
    grid-row: 3;
  }
  #plant8 {
    grid-column: 3;
    grid-row: 3;
  }
  #plant9 {
    grid-column: 4;
    grid-row: 3;
  }
}/*# sourceMappingURL=larger.css.map */